.body{
    background-color: white;
}
.checkoutproduct {
    display: flex;
    flex-direction: row;
    margin-top: 20px;
    margin-bottom: 20px;

}
.checkoutProduct_info{
padding-left: 20px;
}
.checkoutProduct_info >button{
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
